<template>
  <div class="home">
    <NavigationBar/>
    <RegLogModal
      v-if="!isLogged && !continueWithoutLog"
      @withoutLog="withoutLog"
      @loginAccept="loginAccept"
    />
    {{token}}
  </div>
</template>

<script>
// @ is an alias to /src
import RegLogModal from '@/components/RegLogModal.vue';
import NavigationBar from '@/components/NavigationBar.vue'

export default {
  name: 'gamestart',
  components: {
    RegLogModal,
    NavigationBar,
  },
  data: () => ({
    isLogged: false,
    continueWithoutLog: false,
    token: "",
  }),
  methods: {
    withoutLog: function() {
        this.continueWithoutLog = true;
    },
    loginAccept: function(token) {
        this.isLogged = true;
        this.token = token;
    }
  }
};
</script>
