<template>
    <v-toolbar color=#ebac0c fixed>
        <v-menu offset-y>
            <template v-slot:activator="{on}">
                <v-app-bar-nav-icon
                    v-on="on"
                ></v-app-bar-nav-icon>
            </template>
            <v-list>
                <v-list-item
                    v-for="(item, i) in items"
                    :key="i"
                    @click="openTitle(item.title)"
                >
                <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item>
            </v-list>
        </v-menu>
        <v-toolbar-title>Adgame</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn 
            text
        ></v-btn>
        <v-btn 
            color=#ebac0c
        ></v-btn>
    </v-toolbar>
</template>

<script>
export default {
    name: 'NavigationBar',
    data: () => ({
        items: [
            { title: 'About' },
        ], 
    }),
    methods:{
        openTitle: function(title){
            if (title == "About"){
                this.$router.push({ name: 'about' });
            }
        }
    }
};
</script>

